$bg: #242943;
$bg-alt: #2a2f4a;
$fg: #ffffff;
$fg-bold: #ffffff;
$fg-light: rgba(244,244,255,0.2);
$border: rgba(212,212,255,0.1);
$border-bg: rgba(212,212,255,0.035);
$highlight: #9bf1ff;
$accent1: #6fc3df;
$accent2: #8d82c4;
$accent3: #ec8d81;
$accent4: #e7b788;
$accent5: #8ea9e8;
$accent6: #87c5a4;


@import "./css/normalize.css";
/* @import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,300italic,600,600italic");
 */




html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body { 
  background: $bg;
  line-height: 1;

}


@media screen and (max-width: 480px) {

  html, body {
    min-width: 320px;
  }

} 

/* Type */
body, input, select, textarea {
  color: $fg;
  font-family: "Source Sans Pro", Helvetica, sans-serif;
  font-size: 20pt;
  font-weight: 300;
  letter-spacing: 0.025em;
  line-height: 1.65;
}

  @media screen and (max-width: 1680px) {

    body, input, select, textarea {
      font-size: 17pt;
    }

  }

  @media screen and (max-width: 1280px) {

    body, input, select, textarea {
      font-size: 14pt;
    }

  }

  @media screen and (max-width: 360px) {

    body, input, select, textarea {
      font-size: 12pt;
    }

  }

a {
  transition: color 0.2s ease-in-out, border-bottom-color 0.2s ease-in-out;
  border-bottom: dotted 1px;
  color: inherit;
  text-decoration: none;
}

  a:hover {
    border-bottom-color: transparent;
    color: #9bf1ff !important;
  }

  a:active {
    color: #53e3fb !important;
  }

strong, b {
  color: #ffffff;
  font-weight: 600;
}

em, i {
  font-style: italic;
}

p {
  margin: 0 0 2em 0;
}

h1, h2, h3, h4, h5, h6 {
  color: #ffffff;
  font-weight: 600;
  line-height: 1.65;
  margin: 0 0 1em 0;
}

  h1 a, h2 a, h3 a, h4 a, h5 a, h6 a {
    color: inherit;
    border-bottom: 0;
  }

h1 {
  font-size: 2.5em;
}

h2 {
  font-size: 1.75em;
}

h3 {
  font-size: 1.35em;
}

h4 {
  font-size: 1.1em;
}

h5 {
  font-size: 0.9em;
}

h6 {
  font-size: 0.7em;
}

@media screen and (max-width: 736px) {

  h1 {
    font-size: 2em;
  }

  h2 {
    font-size: 1.5em;
  }

  h3 {
    font-size: 1.25em;
  }

}

sub {
  font-size: 0.8em;
  position: relative;
  top: 0.5em;
}

sup {
  font-size: 0.8em;
  position: relative;
  top: -0.5em;
}

blockquote {
  border-left: solid 4px rgba(212, 212, 255, 0.1);
  font-style: italic;
  margin: 0 0 2em 0;
  padding: 0.5em 0 0.5em 2em;
}

code {
  background: rgba(212, 212, 255, 0.035);
  font-family: "Courier New", monospace;
  font-size: 0.9em;
  margin: 0 0.25em;
  padding: 0.25em 0.65em;
}

pre {
  -webkit-overflow-scrolling: touch;
  font-family: "Courier New", monospace;
  font-size: 0.9em;
  margin: 0 0 2em 0;
}

  pre code {
    display: block;
    line-height: 1.75;
    padding: 1em 1.5em;
    overflow-x: auto;
  }

hr {
  border: 0;
  border-bottom: solid 1px rgba(212, 212, 255, 0.1);
  margin: 2em 0;
}

  hr.major {
    margin: 3em 0;
  }


ol, ul {
	list-style: none;
}


blockquote, q {
	quotes: none;
}

	blockquote:before, blockquote:after, q:before, q:after {
		content: '';
		content: none;
	}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

mark {
	background-color: transparent;
	color: inherit;
}

input::-moz-focus-inner {
	border: 0;
	padding: 0;
}

input, select, textarea {
	appearance: none;
}