.row {
  display: flex;
  flex-wrap: wrap;
  box-sizing: border-box;
  align-items: stretch;
}
.row > .col-6 {
  width: 50%;
}
.row > .col-12 {
  width: 100%;
}

@media screen and (max-width: 1680px) {

  .row {
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;
    align-items: stretch;
  }

    .row > * {
      box-sizing: border-box;
    }
    .row {
      margin-top: 0;
      margin-left: -2em;
    }

      .row > * {
        padding: 0 0 0 2em;
      }
  }

  @media screen and (max-width: 1280px) {
		
    .row {
      display: flex;
      flex-wrap: wrap;
      box-sizing: border-box;
      align-items: stretch;
    }

      .row > * {
        box-sizing: border-box;
      }
      .row {
        margin-top: 0;
        margin-left: -1.5em;
      }

        .row > * {
          padding: 0 0 0 1.5em;
        }
    }

    @media screen and (max-width: 980px) {
		
      .row {
        display: flex;
        flex-wrap: wrap;
        box-sizing: border-box;
        align-items: stretch;
      }

        .row > * {
          box-sizing: border-box;
        }
        .row > .col-12-medium {
          width: 100%;
        }
        .row {
          margin-top: 0;
          margin-left: -1.5em;
        }

          .row > * {
            padding: 0 0 0 1.5em;
          }
      }

      @media screen and (max-width: 736px) {
		
        .row {
          display: flex;
          flex-wrap: wrap;
          box-sizing: border-box;
          align-items: stretch;
        }
  
          .row > * {
            box-sizing: border-box;
          }

          .row > .col-12-small {
            width: 100%;
          }
          .row {
            margin-top: 0;
            margin-left: -1.25em;
          }
  
            .row > * {
              padding: 0 0 0 1.25em;
            }
        }

        @media screen and (max-width: 480px) {
		
					.row {
						display: flex;
						flex-wrap: wrap;
						box-sizing: border-box;
						align-items: stretch;
					}
		
						.row > * {
							box-sizing: border-box;
						}
            .row {
							margin-top: 0;
							margin-left: -1.25em;
						}
		
							.row > * {
								padding: 0 0 0 1.25em;
							}




          }

#main {
  background-color: #2a2f4a;
}

  #main > * {
    border-top: solid 1px rgba(212, 212, 255, 0.1);
  }

    #main > *:first-child {
      border-top: 0;
    }

    #main > * > .inner {
      padding: 4em 0 2em 0 ;
      margin: 0 auto;
      max-width: 65em;
      width: calc(100% - 6em);
    }

      @media screen and (max-width: 736px) {

        #main > * > .inner {
          padding: 5em 0 1em 0 ;
          width: calc(100% - 3em);
        }

      }

#main.alt {
  background-color: transparent;
  border-bottom: solid 1px rgba(212, 212, 255, 0.1);
}

#main > * > .inner {
  padding: 4em 0 2em 0 ;
  margin: 0 auto;
  max-width: 65em;
  width: calc(100% - 6em);
}

  @media screen and (max-width: 736px) {

    #main > * > .inner {
      padding: 5em 0 1em 0 ;
      width: calc(100% - 3em);
    }

  }


	header.major {
		width: -moz-max-content;
		width: -webkit-max-content;
		width: -ms-max-content;
		width: max-content;
		margin-bottom: 2em;
	}

		header.major > :first-child {
			margin-bottom: 0;
			width: calc(100% + 0.5em);
		}

			header.major > :first-child:after {
				content: '';
				background-color: #ffffff;
				display: block;
				height: 2px;
				margin: 0.325em 0 0.5em 0;
				width: 100%;
			}

		header.major > p {
			font-size: 0.7em;
			font-weight: 600;
			letter-spacing: 0.25em;
			margin-bottom: 0;
			text-transform: uppercase;
		}


	@media screen and (max-width: 736px) {

		header.major > p br {
			display: none;
		}

	}

  
  ol, ul {
    list-style: none;
  }

	ul {
		list-style: disc;
		margin: 0 0 2em 0;
		padding-left: 1em;
	}

		ul li {
			padding-left: 0.5em;
		}

		ul.alt {
			list-style: none;
			padding-left: 0;
		}

			ul.alt li {
				border-top: solid 1px rgba(212, 212, 255, 0.1);
				padding: 0.5em 0;
			}

				ul.alt li:first-child {
					border-top: 0;
					padding-top: 0;
				}

	dl {
		margin: 0 0 2em 0;
	}

		dl dt {
			display: block;
			font-weight: 600;
			margin: 0 0 1em 0;
		}

		dl dd {
			margin-left: 2em;
		}
